// strip

div.strip {
  padding: ($spacing*1.5) 0;
  background: white;
  @include bp-m {padding: ($spacing*2) 0;}

  &.alt {padding-top: 0;}

  &.section {
    padding: ($spacing*2) 0 ($spacing*1);

    @include bp-m {
      padding: ($spacing*3) 0 ($spacing*2);
      &.latest p.lead span {display: block;}
    }

    div.wrap > h2 {@extend .h1;}
    &[class*="style-"]:not(.style-5) p.lead a {color: white;}
  }

  &.style-1,
  &.style-2,
  &.style-3,
  &.style-4,
  &.style-5 {
    position: relative;
    color: white;

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      @extend .aesthetic;
      background-repeat: repeat;
      background-position: 50%;
      background-size: cover;
      content: "";
      opacity: 1;
    }

    &.faqs:before {
      background-size: 100% auto;
      background-position: 50% 0;
    }

    &:after {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      background-image: linear-gradient(to right, $clr-b2 0%, $clr-b3 100%);
      content: "";
      opacity: 0.8;
    }

    div.wrap {
      position: relative;
      z-index: 2;
    }
  }

  &.style-5 {color: $clr-1;}

  &.style-1:after {background-image: linear-gradient(to right, $clr-b2 0%, $clr-b3 100%);}
  &.style-2:after {background-image: linear-gradient(to right, $clr-b3 0%, $clr-b4 100%);} 
  &.style-3:after {background-image: linear-gradient(to right, $clr-b2 0%, $clr-b1 100%);} 
  &.style-5:after {background-image: linear-gradient(to right, $clr-3 0%, $clr-4 100%);}

  &.style-2:before,
  &.style-5:before {transform: rotate(180deg);}

  // getting started

  &.started {
    text-align: center;
    p {@extend .align-middle;}
  }

  // contact - just for contact pages

   body.contact & {
    @include bp-m {div.row > div:first-child {order: 2;}}
  }

  // call to actions

  &.cta {
    position: relative;
    z-index: 50;
    padding: ($spacing*1.5) 0;
    background-color: $clr-2;
    text-align: center;
    color: white;

    @include bp-m {
      text-align: left;

      &.download {padding: $spacing 0;}
    }

    div.wrap {

      @include bp-m {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      @include bp-l {align-items: flex-end;}

      > h2 {
        @extend .lead;
        margin: 0;

        @include bp-m {
          flex: 0 1 auto;
          align-self: center;
        }

        > span {
          font-size: $fs-20;
          @include bp-m {display: inline-block;}

          &:before {
            margin: 0 0.4rem 0 0.25rem;
            content: "\02014";
          }         

          span {
            @include bp-s {white-space: nowrap;}
          }
        }
      }

      > p {
        margin: $vmargin auto 0;

        @include bp-m {
          flex: 0 0 auto;
          align-self: center;
          margin: 0 0 0 $spacing;
        }

        a.button {
          @include bp-s-less {display: block;}
        }

        span.other {
          display: block;
          margin-top: 0.75rem;
          font-size: $fs-16;
          text-align: center;

          a {
            color: white;
          }
        }
      }
    }

    @include bp-l {
      &.download span.image:before {bottom: -($spacing);}
    }

    &.getting-started {
      text-align: center;

      div.wrap {
        display: block;

        h2 {@extend .h1;}
        p {margin: $vmargin auto;}
        ul {margin: $vmargin 0 0;}
      }
    }

    &.contact {
      text-align: center;

      div.wrap {display: block;} /* required reset */

      div.col {

        h2 {@extend .lead;}

        label {@include bp-s-less {display: block;}}

        > p {
          @include bp-m {margin: 0;}
        }
      }
    }

    &.newsletters {
      div.compact-form {
        margin: 0;

        fieldset {
          @include bp-m {
            display: flex;
            align-items: center;
          }

          > p {
            @extend .h3;
            font-weight: 300;

            @include bp-m {
              flex: 0 1 auto;
              align-self: center;
              margin: 0;
              max-width: 60%; // IE 11 bug fix to make the container shrink on smaller screens
            }
          }

          div.form-group {
            margin: 0 auto;
            max-width: 520px;
            
            @include bp-m {
              flex: 1 1 auto;
              margin: 0 0 0 $spacing;
              min-width: 450px;
            }
          }
        }
      }
    }
  }
}

// page intro

div.page-intro {
  padding: ($spacing*4) 0 ($spacing*2);
  text-align: center;

  @include bp-m {padding: ($spacing*5.5) 0 ($spacing*2.5);}

  body.about & {
    @include bp-m-less {padding-bottom: 0;}
  }

  h1 {
    margin-bottom: 0;
    @include bp-l {font-size: $fs-70;}

    body.error & {
      @extend .h2;
      span {
        display: block;
        font-size: $fs-70;
        @include bp-m {font-size: $fs-100;}
      }
    }
  }

  p.lead {
    margin: $vmargin auto 0;
    @include bp-m {max-width: 53rem;}

    body.form-response & {
      display: inline-block;
      border-radius: $border-radius;
      padding: $spacing;
      background: $clr-4;
    }

    span {@include bp-m {display: block;}}
  }

  p.cta {margin: $vmargin 0 0;}
}

// home intro

div.home-intro {
  @extend .page-intro;
  overflow: hidden;
  position: relative;
  background: white;
  color: white;

  div.wrap {
    position: relative;
    z-index: 10;
  }

  h1 {
    @include bp-s-less {font-size: $fs-25;}

    span {display: block;}
  }

  p.lead {
    margin-bottom: $vmargin;
    @include bp-m {margin-bottom: $vmargin*2;}
  }

  ul.intro-funnels {
    div.content {
      position: relative;
      padding: $spacing;
      background: rgba(black,0.1);
      text-align: center;

      @include bp-m {padding: ($spacing*2);}

      div.text {
        position: relative;
        z-index: 2;

        p {
          font-weight: 300;
          @include bp-s {font-size: $fs-22;}
          @include bp-m {font-size: $fs-25;}

          &:last-child {margin-bottom: 0;}
        }
      }
    }
  }
}

// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  div.page-intro & {
    margin-top: ($vmargin*2);
  }

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}

      &.col-4,
      &.col-6 {
        @include bp-m {&:nth-child(2) {margin-top: 0;}}
      }

      &.col-4 {
        @include bp-m {&:nth-child(3) {margin-top: 0;}}
      }
    }
  }
}

div.wrap > h2 + div.general-wrap,
div.wrap > h2 + p.lead + div.general-wrap {
  margin-top: $vmargin*1.5;
  @include bp-m {margin-top: $vmargin*2;}
}

// box 

div.box {
  overflow: hidden;
  margin: 0 0 $vmargin;
  border-radius: $border-radius;
  background: $clr-4;

  > div:first-of-type {margin-top: 0;}
  > :last-child {margin-bottom: 0;}

  div.col-4 & {font-size: $fs-18;}

  h2 {
    @extend .h3;
    margin: 0;
    background: $clr-2;

    a,
    span {
      display: block;
      padding: ($spacing/2) $spacing;
      color: white;
      text-decoration: none;
      transition: background-color 0.2s;
    }

    a {
      @include bp-m {
        &:hover,
        &:focus {background: rgba(black,0.2);}
      }
    }
  }

  div.content {
    padding: $spacing;
    > :last-child {margin-bottom: 0;}
  }

  // secondary nagivation

  nav {

    @include bp-m {padding-bottom: 0.5rem;}

    ul {
      margin: 0;

      li {
        a {
          display: block;
          padding: 0.5rem $spacing;
          text-decoration: none;
          color: $clr-1;
          transition: background-color 0.2s, color 0.1s;

          @include bp-m {
            &:hover,
            &:focus {
              background: rgba(white,0.6);
              color: $clr-b1;
            }
          }

          &.current {
            background: rgba(white,0.6);
            color: $clr-b1;
            box-shadow: inset 5px 0 $clr-b1;
          }
        }

        ul {
          font-size: $fs-17;
          li a:before {content: "\02013\000A0";}
        }
      }
    }
  }
}

// notices

p.notice {
  position: relative;
  border: 3px solid $clr-3;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;

  &:before {
    position: absolute;
    left: 0.8rem; top: 1.05rem;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-b1;
    background: rgba($clr-b1,0.05);
    text-align: center;

    &:before {content: none;}

    div.buy-details & {
      border-color: white;
      background: rgba(white,0.1);

      span {
        display: block;
        font-size: $fs-18;
      }

      b {font-size: $fs-22;}
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }
}

// contact form inline

input#contact-form-inline {
  @include hide-screen();

  &:checked {
    + div.row {
      label.contact-form-inline-cta span[class*="icon-"]:before {@extend .icon-minus-circle;}
      + div.contact-form-inline {display: block;}
    }
  }
}

div.contact-form-inline {
  display: none;
  margin: $vmargin auto 0;
  border-radius: $border-radius;
  padding: $spacing;
  max-width: 30rem;
  background: $clr-1;

  form button.button {
    @include bp-s-less {width: 100%;}
  }
}

// share links

div.share {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-bottom: $vmargin;
  border-top: 5px solid $clr-b1;
  padding-top: $spacing;

  @include bp-m {margin-bottom: 0;}

  ul.share-options {flex: 1 0 auto;}

  p {
    flex: 0 0 auto;
    margin-bottom: 0;

    a {
      display: block;
      border-radius: 2rem;
      width: 1.7rem;
      height: 1.7rem;
      line-height: 1.9rem;
      background: $clr-b1;
      text-decoration: none;
      text-align: center;
      font-size: $fs-14;
      color: white;
      transition: box-shadow 0.1s;

      @include bp-m {
        &:hover,
        &:focus {
          box-shadow: inset 0 0 0 4rem rgba(black,0.1);
        }
      }

      span {@include hide-screen();}
    }
  }
}

// contact button

input.contact-button-toggle {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
  visibility: hidden;

  &:checked + div.contact-button {

    label.cta {
      display: block;

      span.button {
        box-shadow: inset 0 1px 2px rgba(black,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-s {content: "";}}
    }

    div.form {
      top: 0; right: 0;

      @include bp-s-less {left: 0;}

      @include bp-s {
        top: auto;
        margin-bottom: 5rem;
        opacity: 1;
        transition: margin-bottom 0.25s, opacity 0.25s;
      }
    }
  }
}

div.contact-button {
  position: fixed;
  bottom: 0; right: 0;
  z-index: 1000;
  padding-bottom: 1rem;

  label.cta {
    display: none;
    cursor: pointer;

    @include bp-s {display: block;}

    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0!important;
      width: 2.5rem;
      height: 2.5rem;

      &:before {
        margin: 0!important;
        font-size: $fs-25;
      }

      @include bp-m {
        width: 3rem;
        height: 3rem;

        &:before {font-size: $fs-30;}
      }


      span {@include hide-screen();}
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.form {
    position: fixed;
    bottom: 0; right: 1000em;
    z-index: 1;
    background: $clr-4;

    @include bp-s {
      margin: 0 1rem 4rem 0;
      border-radius: $border-radius;
      width: 16rem;
      background: none;
      opacity: 0;
      box-shadow: 0.25rem 0.25rem 1.5rem rgba(black,0.3);
      transition: margin-bottom 0.25s, opacity 0.25s, right 0s 0.25s;
    }

    h2 {
      @extend .h3;
      position: relative;
      margin: 0;
      background: $clr-2;
      color: white;
      padding: 0.5rem 1rem;

      @include bp-s {border-radius: $border-radius $border-radius 0 0;}

      &:after {
        position: absolute;
        right: 1rem;
        top: 0;
        border-radius: 3rem;
        width: 3rem;
        height: 3rem;
        background: white url(../../assets/images/content/other/contact-button-person.jpg) no-repeat 50%;
        background-size: cover;
        box-shadow: 0 0 0 5px $clr-2;
        content: "";

        @include bp-s {top: -0.15rem;}
      }
    }

    form {
      padding: 1rem;
      background: $clr-4;

      @include bp-s {border-radius: 0 0 $border-radius $border-radius;}

      div.form-group {
        margin: 0 0 ($vmargin/2);

        &.linear-fields {
          margin-bottom: 0;

          label {
            margin-top: 0;
            margin-bottom: ($vmargin/2);
          }
        }

        input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
        textarea,
        .custom-select {
          padding: 0.4rem 0.5rem;
          font-size: $fs-18;
        }
      }

      button {
        width: 100%;
        font-size: $fs-20;
      }
    }
  }
}
