// spaced list

.spaced {
  li {margin: 0.5rem 0;}
}

// disc list

.disc {
  margin-left: 1.25rem;
  list-style: disc;

  ul {
    margin-left: 1.25rem;
    list-style: circle;
  }
}

// decimal list

.decimal {
  margin-left: 1.25rem;
  list-style: decimal;
}

// lower alpha list

.lower-alpha li {
  margin-left: 1.25rem;
  list-style: lower-alpha;

  ol {margin-bottom: 0;}
}

// defintion list

dt {
  @extend .h4;
  font-weight: 500;
}

// checked list

ul.checked {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      position: absolute;
      left: 0; top: 0.2rem;

      body.consulting & {color: $clr-b2;}
      body.academy &,
      ol.events & {color: $clr-b3;}
      body.resources & {color: $clr-b4;}
      div.about-success & {color: $clr-2;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// buttons

ul.buttons {

  div.service-process div.text & {margin-bottom: 0;}

  li {
    display: inline-block;
    margin-bottom: 0.5rem;
    margin-right: 0.2rem;

    @include bp-s-less {display: block;}

    .button {
      @include bp-s-less {display: block;}
    }
  }
}

// questions

ul.questions {
  @extend .lead;
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  text-align: left;

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.2rem;
    }
  }
}

// hierarchy links

div.hierarchy {
  padding: 0.4rem 0;
  background: $clr-4;

  ol {
    margin: 0;
    font-size: $fs-16;

    li {
      display: inline-block;

      &:before {
        @extend .icon-extension, .icon-angle-left;
        margin-right: 0.25rem;
        font-size: $fs-10;
        vertical-align: middle;
      }
    
      a, span {
        text-decoration: none;
        vertical-align: middle;
        color: #3B3D41;
      }
      
      a {
        @include bp-m {
          &:hover,
          &:focus {
            text-decoration: underline;
            color: $clr-b1;
          }
        }
      }
    }
  }
}

// brand assets

ul.brand-assets {
  border-top: 1px solid $clr-3;

  li {
    border-bottom: 1px solid $clr-3;
    padding: 0.4rem 0;

    a {
      text-decoration: none;
      @include bp-m {
        &:hover,
        &:focus {text-decoration: underline;}
      }
    }
  }
}

// chevroned list

ul.chevroned {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-angle-right;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;

      body.consulting & {color: $clr-b2;}
      body.academy & {color: $clr-b3;}
      body.resources & {color: $clr-b4;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// document list

ul.documents {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-file;
      position: absolute;
      left: 0; top: 1px;
      font-size: $fs-24;
    }
  }
}

// numbered discs

ol.numbered-discs {
  counter-reset: numbered-discs;

  li {
    position: relative;
    margin-bottom: 0.5rem;
    padding-left: 2rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      margin-right: 1rem;
      border-radius: 50%;
      padding: 0;
      width: 1.3rem;
      height: 1.3rem;
      line-height: 1.4rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 700;
      font-size: $fs-16;
      color: white;
      counter-increment: numbered-discs;
      content: counter(numbered-discs);

      body.consulting & {background: $clr-b2;}
      body.academy & {background: $clr-b3;}
      body.resources & {background: $clr-b4;}
    }
  }
}

// numbered blocks

ol.numbered-blocks {
  counter-reset: numbered-blocks;
  margin-top: 2rem;
  text-align: center;

  @include bp-m {
    margin-top: 0;
    text-align: left;
  }

  &.alt.alt li:before {
    @extend .icon-extension,.icon-check;
    background: $clr-b1;
  }

  li {
    position: relative;
    margin-bottom: 4px;
    padding: ($spacing*1.25) $spacing;
    background: $clr-4;

    &:first-child {border-radius: $border-radius $border-radius 0 0;}
    &:last-child {border-radius: 0 0 $border-radius $border-radius;}

    @include bp-m {padding-left: $spacing*1.75;}

    &:before {
      position: absolute;
      right: 50%; top: 0;
      margin: -1.3rem -1.25rem 0 0;
      border-radius: 50%;
      border: 4px solid white;
      padding-top: 0.35rem;
      width: 2.5rem;
      height: 2.5rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 400;
      font-size: $fs-20;
      color: white;
      counter-increment: numbered-blocks;
      content: counter(numbered-blocks);
      box-sizing: border-box;

      @-moz-document url-prefix() {padding-top: 0.4rem;}

      body.consulting & {background: $clr-b2;}
      body.academy & {background: $clr-b3;}
      body.resources & {background: $clr-b4;}

      @include bp-m {
        right: 100%; top: 50%;
      }
    }

    > :last-child {margin-bottom: 0;}
  }
}

// contact options

ul.contact-options {
  li {
    position: relative;
    padding-left: 1.5rem;

    &:before {
      position: absolute;
      top: 0.2rem; left: 0;
      font-size: $fs-20;

      footer.footer & {top: 0.05rem;}
    }

    &.email {
      overflow-wrap: break-word;
      word-wrap: break-word;

      span {
        @include bp-s-less {@include hide-screen();}
        footer & {@include hide-screen();}
      }
    }

    &.address {
      div.box &,
      footer & {@include hide-screen();}
      span {display: block;}
    }

    a {
      text-decoration: none;
      
      main & {
        @include bp-m {
          &:hover,
          &:focus {text-decoration: underline;}
        }
      }
    }

    span.icon-angle-right:before {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0.5rem;
      font-size: $fs-10;
    }
  }
}

// site map

ul.site-map {
  overflow: hidden;
  border-radius: $border-radius;

  > li {

    a {
      display: block;
      border-bottom: 1px solid white;
      padding: 0.5rem 1rem;
      background: $clr-3;
      text-decoration: none;
      color: $clr-1;

      @include bp-m {
        &:hover,
        &:focus {
          color: $clr-b1;
          box-shadow: inset 0 0 0 100rem rgba($clr-2,0.05);
        }
      }
    }

    ul {
      margin: 0;
      font-size: $fs-18;
      
      li a {
        padding-left: 2rem;
        background: $clr-4;
      }
    }

    &:last-child a {border-bottom: 0;}
  }
}

// faqs 

ol.faqs {
  counter-reset: faq-item;

  li {
    margin: 0 0 4px;
    counter-increment: faq-item;

    &:first-of-type h3 label  {border-radius: $border-radius $border-radius 0 0;}
    &:last-of-type h3 label,
    &:last-of-type div.answer  {border-radius: 0 0 $border-radius $border-radius;}
    &:last-of-type input:checked + h3 label {border-radius: 0;}

    input {
      @include hide-screen();

      + h3 {
        @extend .h4;
        margin: 0;
        color: $clr-b1; // important for when in a dark background strip
          
        label {
          position: relative;
          display: block;
          margin: 0;
          padding: $spacing;
          padding-right: 3rem;
          background: rgba($clr-4,0.7);
          font-weight: 400;
          color: $clr-1;
          cursor: pointer;
          user-select: none;
          -webkit-user-select: none;

          &:before {
            @extend .icon-extension,.icon-plus-circle;
            position: absolute;
            top: 50%; right: 0;
            z-index: 1;
            margin: -0.5rem 1rem 0 0;
            font-size: 1em;
            color: $clr-b1;
          }

          @include bp-m {
            &:hover,
            &:focus {
              background: $clr-3;
              color: $clr-b1;
            }
          }

          span:before {
            content: counter(faq-item) ". ";
          }
        }

        + div.answer {
          position: absolute;
          left: -1000em;
          padding: $spacing;
          background: $clr-b5;
          color: $clr-1;
          -webkit-text-size-adjust: 100%; // forcing iOS to stop increasing text size

          div.section.style-5 & {background: white;}

          :last-child {margin-bottom: 0;}
        }
      }

      &:checked {
        + h3 {

          label {
            background: $clr-2;
            color: white;

            &:before {
              @extend .icon-minus-circle;
              color: white;
            }
          }

          + div.answer {position: static;}
        }
      }
    }
  }
}

// elements - general lists with/out links and icons

.elements.elements {
  overflow: hidden;
  margin: 0;
  background: $clr-4;

  &.ctas {background: none;}

  &.two {
    li.col {
      @include bp-m {
        &:nth-child(odd) {padding-right: 2px;}
        &:nth-last-child(2) {padding-bottom: 0;}
        &:nth-child(even) {padding-left: 2px; padding-bottom: 4px;}
        &:nth-last-child(1) .content {border-radius: 0 0 $border-radius 0;}
      }
    }
  }

  &.three {
    li.col {
      @include bp-m {
        &:nth-child(odd) {padding-right: 2px;}
        &:nth-child(even) {padding-left: 2px; padding-right: 2px;}
        &:nth-child(3n) {padding-left: 2px;}
        &:nth-last-child(3) {padding-bottom: 0;}
        &:nth-last-child(2) {padding-bottom: 0;}
      }
    }
  }

  > li,
  li.col {
    border: 0 solid $clr-4;
    margin: 0;
    padding: 0;
    padding-bottom: 4px;

    @include bp-m {
      display: flex;
      flex: 0 1 auto;
    }

    &:last-child {padding-bottom: 0!important;}

    .content {
      position: relative;
      display: block;
      padding: ($spacing) 0;
      background: white;
      text-decoration: none;
      transition: background-color 0.15s;

      .style-1 &,
      .style-2 &,
      .style-3 & {
        background: rgba(black,0.1);
        color: white;
      }

      @include bp-m {
        flex: 1;
        padding: ($spacing*2);
      }

      &[class*="icon-"] {
        text-align: center;

        &:before {
          display: block;
          margin-bottom: 1rem;
          font-size: $fs-40;
          color: $clr-b1;

          @include bp-m {font-size: $fs-50;}

          .style-1 &,
          .style-2 &,
          .style-3 & {color: white;}
        }
      }

      h2,h3 {
        @extend .h2;
        margin-bottom: 1rem;
        span {display: block;}
      }

      > :last-child {margin-bottom: 0;}
    }

    a.content {
      padding: $spacing;
      padding-bottom: $spacing*2;
      background: $clr-4;

      &:after {
        position: absolute;
        bottom: 0; left: 50%;
        display: inline-block;
        margin: 0 0 0 -4.25rem;
        border-radius: 0.25rem 0.25rem 0 0;
        padding: 0.2rem 0 0.1rem;
        width: 8.5rem;
        background: white;
        text-align: center;
        font-size: $fs-18;
        color: $clr-b1;
        content: "Learn more";
        transition: opacity 0.2s;
      }

      @include bp-m {
        padding: $spacing*1.4;
        padding-bottom: $spacing*1.75;

        &:after {opacity: 0;}

        &:hover,
        &:focus {
          background: $clr-3;
          &:after {opacity: 1;}
        }
      }

      p,ul {color: $clr-1;}
    }
  }

  // benefits

  &.benefits {
    overflow: visible;
    background: none;

    li {
      &:first-child div.content {border-radius: $border-radius $border-radius 0 0;}
      &:last-child div.content {border-radius: 0 0 $border-radius $border-radius;}

      div.content {
        position: relative;
        padding: $spacing ($spacing*2);
        background: rgba(white,0.5);
        text-align: left;

        .style-2 &,
        .style-3 & {
          background: rgba(black,0.1);
        }

        &:before {
          position: absolute;
          right: 100%; top: 50%;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 50%;
          padding: 0;
          width: 2.5rem;
          height: 2.5rem;
          line-height: 2.5rem;
          background: white;
          text-align: center;
          font-weight: 700;
          font-size: $fs-27;
          color: $clr-b1!important;
          box-sizing: border-box;
          box-shadow: 1px 1px 2px rgba(black,0.3);
        }

        span {
          @extend .h3;
        }
      }


    }
  }
}

// team

ul.team {
  position: relative;
  text-align: center;

  > li {
    position: relative;
    @include bp-m {position: static;}

    div.content {
      display: flex;
      flex: 1;

      h4 {
        @extend .h2;
        position: relative;
        display: flex;
        flex: 1;
        margin: 0;

        label {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          flex: 1;
          padding: ($spacing*1.5) $spacing ($spacing*2.5);
          background: $clr-4;
          cursor: pointer;
          transition: background-color 0.2s, color 0.2s;

          &:after {
            position: absolute;
            bottom: 0; left: 50%;
            display: inline-block;
            margin: 0 0 0 -3.5rem;
            border-radius: 0.25rem 0.25rem 0 0;
            padding: 0.2rem 0 0.1rem;
            width: 7rem;
            background: white;
            text-align: center;
            font-size: $fs-18;
            font-weight: 400;
            line-height: $lh-general;
            color: $clr-b1;
            content: "View profile";
            transition: opacity 0.2s;
          }

          @include bp-m {
            padding: ($spacing*2.25) $spacing;

            &:after {opacity: 0;}

            &:hover,
            &:focus {
              background: $clr-3;
              &:after {opacity: 1;}
            }
          }

          @include bp-l {
            padding: ($spacing*1.75) $spacing;
          }

          span.image {
            display: block;
            position: relative;
            overflow: hidden;
            margin: 0 auto $vmargin;
            border-radius: 50%;
            width: 200px;

            img {
              position: absolute;
              top: 0; left: 0;
              border-radius: 50%;
              width: 100%;
            }

            &:before {
              display: block;
              padding-top: 100%;
              content: "";
            }
          }

          span {
            &.name {
              font-weight: 300;  
              color: $clr-b1;
            }

            &.title {
              display: block;
              margin-top: 0.25rem;
              font-weight: 400;
              font-size: $fs-18;
            }
          }
        }

        a.linkedin.linkedin {
          display: none;
          margin-top: 0.75rem;
          padding-left: 0.6rem;
          padding-right: 0.6rem;

          @include bp-m {
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 0; left: 50%;
            margin: 75px 0 0 125px;
          }

          &:before {
            margin: 0;
            font-size: $fs-20;
            color: white;
          }

          span {@include hide-screen();}
        }
      }

      div.text {
        position: absolute;
        left: -1000em;
        align-items: flex-start;
        text-align: left;
        color: white;

        a.button span {@include hide-screen();}

        > div:last-child p {margin-bottom: 0;}
      }
    }

    input {
      @include hide-screen();

      &:checked + div.content {
        display: block; // reset flex
        padding: ($spacing*1.5) $spacing;
        background: $clr-2;

        @include bp-m {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1; // required
          padding: $spacing*2;
        }

        &:after {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1;
          @extend .aesthetic;
          background-repeat: no-repeat;
          background-position: 50%;
          background-size: cover;
          opacity: 0.1;
          content: "";
        }

        h4,
        div.text {
          position: relative;
          z-index: 2;
          left: auto;
          > :last-child {margin-bottom: 0;}
        }

        h4 {
          display: block; // reset flex
          margin-bottom: ($vmargin/2);
          color: white;

          @include bp-m {margin-bottom: $vmargin;}

          label {
            position: static;
            display: block;
            padding: 0;
            min-height: 0;
            background: none;
            cursor: default;
            transition: none;

            @include bp-m {
              display: inline-block;

              &:hover,
              &:focus {
                span.name {text-decoration: none;}
              }

              &:after {content: none;}
            }

            &:before {
              @extend .icon-extension,.icon-times;
              display: flex;
              align-items: center;
              justify-content: center;
              position: absolute;
              right: -0.5rem; top: -1rem;
              border-radius: 50%;
              width: 1.5rem;
              height: 1.5rem;
              background: white;
              text-align: center;
              font-size: $fs-20;
              color: $clr-2;
              cursor: pointer; 

              @include bp-m {
                right: -1rem; top: -1rem;
                width: 2rem;
                height: 2rem;
                font-size: $fs-24;
              }
            }

            &:hover:before {
              background: $clr-b1;
              color: white;
              transition: color 0.2s, background-color 0.2s;
            }

            span.name {color: white;}
          }

          a.linkedin {
            display: inline-block;
            @include bp-m {display: flex;}
          }
        }
      }
    }
  }
}

// information

ul.information {
  text-align: left;

   div.content {
    flex: 1;
    background: $clr-4;

    h3 {
      @extend .h2;
      margin: 0;
      padding: ($spacing*0.75) $spacing;
      background: $clr-3;
      text-align: center;
    }

    p {
      margin: 0;
      padding: $spacing;
      font-size: $fs-22;

      @include bp-m {padding: $spacing*1.5;}

      em {
        display: block;
        margin-bottom: $vmargin/2;
        font: italic $fs-30 Georgia, Serif;
      }
    }

    div.strip.style-5 & {
      background: rgba(white,0.5);
      h3 {background: rgba(white,0.6);}
    }

    div.strip.style-1 &,
    div.strip.style-2 &,
    div.strip.style-3 &,
    div.strip.style-4 & {
      background: rgba(black,0.1);
      h3 {background: rgba(black,0.1);}
    }
  }
}

// share options

ul.share-options {
  margin-bottom: 0;
  font-size: 1rem;

  li {
    display: inline-block;
    margin-right: 0.25rem;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3rem;
      width: 1.7rem;
      height: 1.7rem;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      &.icon-linkedin {background: #0077B5;}
      &.icon-twitter {background: #55ADEE;}
      &.icon-facebook {background: #4267B2;}
      &.icon-envelope {background: #7D7D7D;}

      @include bp-m {
        &:hover,
        &:focus {box-shadow: inset 0 0 0 4rem rgba(black,0.1);}
      }

      span {@include hide-screen();}
    }

    &:last-child {margin-right: 0;}
  }
}
